.issues_creation{
	width: 980px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	margin: 20px 0 20px 0;
}

.issues_creation_button{
	font-family: inherit;
	color: #fff;
    background-color: #28a745;
	background-image: -webkit-gradient(linear,left top, left bottom,from(#34d058),color-stop(90%, #28a745));
	background-image: -webkit-linear-gradient(top,#34d058 0%,#28a745 90%);
	background-image: -o-linear-gradient(top,#34d058 0%,#28a745 90%);
	background-image: linear-gradient(-180deg,#34d058 0%,#28a745 90%);
	
	position: relative;
    display: inline-block;
    padding: 6px 12px;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    white-space: nowrap;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-repeat: repeat-x;
    background-position: -1px -1px;
    background-size: 110% 110%;
    border: 1px solid rgba(27,31,35,0.2);
    border-radius: 0.25em;
    -webkit-appearance: none;
    text-decoration: none;
}

.issues_creation_button[disabled]{
    color: rgba(255,255,255,0.75);
    background-color: #94d3a2;
    background-image: none;
    border-color: rgba(27,31,35,0.2);
    -webkit-box-shadow: none;
            box-shadow: none;
    cursor: default;
}

.issues_creation_button:not([disabled]):hover{
	background-color: #269f42;
    background-image: -webkit-gradient(linear,left top, left bottom,from(#2fcb53),color-stop(90%, #269f42));
    background-image: -webkit-linear-gradient(top,#2fcb53 0%,#269f42 90%);
    background-image: -o-linear-gradient(top,#2fcb53 0%,#269f42 90%);
    background-image: linear-gradient(-180deg,#2fcb53 0%,#269f42 90%);
    background-position: -0.5em;
    border-color: rgba(27,31,35,0.5);
}

.issues_creation_button:active{
	background-color: #279f43;
    background-image: none;
    border-color: rgba(27,31,35,0.5);
    -webkit-box-shadow: inset 0 0.15em 0.3em rgba(27,31,35,0.15);
            box-shadow: inset 0 0.15em 0.3em rgba(27,31,35,0.15);
}

.issues_creation_button:focus{
	-webkit-box-shadow: 0 0 0 0.2em rgba(52,208,88,0.4);
	        box-shadow: 0 0 0 0.2em rgba(52,208,88,0.4);
	outline: none;
}

.issues_list{
	width: 980px;
	display: block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
	border: 1px solid #e1e4e8;
}

.issues_list:empty{
    height: 1350px;
 
    background-image:
			-webkit-linear-gradient( 350deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0) 80% ), 
			-webkit-radial-gradient( circle 7px at 27px 22px, lightgray 99%, transparent 0 ),
			-webkit-linear-gradient( lightgray 15px, transparent 0 ),
			-webkit-linear-gradient( lightgray 10px, transparent 0 ),
			-webkit-linear-gradient( lightgray 15px, transparent 0 );
 
    background-image:
			-o-linear-gradient( 350deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0) 80% ), 
			-o-radial-gradient( circle 7px at 27px 22px, lightgray 99%, transparent 0 ),
			-o-linear-gradient( lightgray 15px, transparent 0 ),
			-o-linear-gradient( lightgray 10px, transparent 0 ),
			-o-linear-gradient( lightgray 15px, transparent 0 );
 
    background-image:
			linear-gradient( 100deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0) 80% ), 
			radial-gradient( circle 7px at 27px 22px, lightgray 99%, transparent 0 ),
			linear-gradient( lightgray 15px, transparent 0 ),
			linear-gradient( lightgray 10px, transparent 0 ),
			linear-gradient( lightgray 15px, transparent 0 );

    background-repeat: repeat-y;

    background-size:
        28px 54px, /* highlight */
        56px 54px, /* circle */
        550px 54px,
        350px 54px,
        20px 54px;

    background-position:
        0 0, /* highlight */
        0 0, /* circle */
        50px 15px,
        50px 35px,
        930px 15px;

    -webkit-animation: shine 1s infinite;

            animation: shine 1s infinite;
}

@-webkit-keyframes shine {
    to {
        background-position:
            100% 0, /* move highlight to right */
            0 0,
            50px 15px,
            50px 35px,
            930px 15px;
    }
}

@keyframes shine {
    to {
        background-position:
            100% 0, /* move highlight to right */
            0 0,
            50px 15px,
            50px 35px,
            930px 15px;
    }
}

.issues_list_item{
	border-bottom: 1px solid #e1e4e8;
}

.issues_list_item:hover{
	background-color: #f6f8fa;
}

.issues_list .issues_list_item:last-child{
	border-bottom: none;
}


